// Define typography
html {
  font: $font-style $font-variant $font-weight #{$font-size}/#{$line-height} $font-family;
}

// Document Global Styles
.container {
    max-width: 50em;
}

body {
    background-color: $body-bg;
    border-top: 3px solid $black;
}

// Vertically center anything
.vertical-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
